	export default {
		data() {
			return {
				useUser:this.user,
				flag:false,
				dj:1,
				i:0,
				list:[
				],
				jg:0,
				wd:this.sf?'工人':'商家',
				mysf:this.sf
			}
		},
		onLoad() {
			this.login()
		},
		methods: {
			back : function(){
			   uni.navigateBack({
			  
			   });
