								let usurl=that.url1+"/dinghai/user/updateMarchant";
								uni.request({
									url: usurl,
									header:{
										"Content-Type" : "application/x-www-form-urlencoded"
									},
									method:'POST',
									data: {
										sid:this.user.uid,
										Merchantprice:this.jg,
										Merchantend:this.list[this.i].businessDate=="包月"?2592000:this.list[this.i].businessDate=="包季"?7776000:this.list[this.i].businessDate=="半年"?15768000:this.list[this.i].businessDate=="包年"?31536000:0
									},
								   success: (res) => {
									   console.log(res)
									   uni.hideLoading()
									if(res.statusCode==200){
										if(res.data.flag==200){
											uni.showToast({
												title:"恭喜您充值成功！"
											})
											uni.navigateBack({
												
											})
										}else{
											
											uni.showModal({
												title:'温馨提示',
												content:'您的金额不足是否要充值',
												success(res) {
													if(res.confirm){
														uni.redirectTo({
															url:'../jine/chongzhi/chongzhi'
														})
													}else{
														
													}
												}
											})
										}
									}else{
										uni.showToast({
											title:"您的网络打了个盹！",
											icon:"none"
											})
									}
								   },
								fail : function(err){
									uni.showToast({
										title:"您的网络打了个盹！",
										icon:"none"
										})
									}
								});
							}
						}else{
							uni.showToast({
								title:"您取消了充值操作！",
								icon:"none"
								})
						}
					}
					})
				}
			}
		 },
		 watch:{
			 i(val){
				 if(this.sf){
					 this.jg=this.list[val].memberprice
				 }else{
					 this.jg=this.list[val].businessprices
				 }
			 }
		 }
}
